import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
export default new Router({
	routes: [
		{
			path: '/',
			component: resolve => require(['../components/page/Home.vue'],resolve),
			children: [
				{
					path: '',
					component: resolve => require(['../components/home/HomePage.vue'], resolve),
				},
				{
					path: '/newhome',
					component: resolve => require(['../components/new/NewHome.vue'], resolve),
				},
				{
					path: '/enterable',
					component: resolve => require(['../components/enterable/particepate/EnterableHome.vue'], resolve),
				},
				// 超级管理员
				{
					path: 'Administrators',
					component: resolve => require(['../components/super/Administrators.vue'], resolve),
				},
				{
					path: 'SuperReference',
					component: resolve => require(['../components/super/SuperReference.vue'], resolve),
				},
				{
					path: 'Personnel',
					component: resolve => require(['../components/super/Personnel.vue'], resolve),
				},
				{
					path: 'superView/:id',
					component: resolve => require(['../components/super/superView.vue'], resolve),
				},
				{
					path: 'superEdit/:id',
					component: resolve => require(['../components/super/superEdit.vue'], resolve),
				},
				{
					path: 'WatchPdf/:id',
					component: resolve => require(['../components/super/WatchPdf.vue'], resolve),
				},
				//导演端
				{
					path: '/direct',
					component: resolve => require(['../components/enterable/direct/DirectHome.vue'], resolve),
					children: [
						{
							path: '',
							component: resolve => require(['../components/enterable/direct/DirectGrouping.vue'], resolve),
						},
						{
							path: 'start',
							component: resolve => require(['../components/enterable/direct/DirectStart.vue'], resolve),
						},
					],
				},
				{
					path: '/HistoricalRehearsal/:id',
					component: resolve => require(['../components/page/HistoricalRehearsal.vue'],resolve),
				},
				{
					path: '/set',
					component: resolve => require(['../components/page/set.vue'],resolve),
				},
				{
					path: '/Reference',
					component: resolve => require(['../components/page/Reference.vue'],resolve),
				},
				//参演端
				{
					path: '/Participate',
					component: resolve => require(['../components/enterable/particepate/Participate.vue'],resolve),
				},
				{
					path: '/practiceStart',
					component: resolve => require(['../components/enterable/particepate/practiceStart.vue'],resolve),
				},
			]
		},
		{
			path: '/login',
			component: resolve => require(['../components/page/Login.vue'],resolve),
		},
		{
			path: '/maxplay',
			component: resolve => require(['../components/enterable/iMax/MaxPlay.vue'],resolve),
		},
		{
			path: '/MaxGroup',
			component: resolve => require(['../components/enterable/iMax/MaxGroup.vue'],resolve),
		}
	],
})


