import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        showHeader: '1',//顶部选中项
        status: false,  //是否是从首页的可观看的演练进入
        setStatus:'0',  //
		loading: true,
	    host:'',//主机地址
	    protocol:'',//主机协议


		// material_path: '/emergency_drill/public/api/attachment',                                     //nas
		// pdf_path: '/emergency_drill/public/api/database',			                                //nas
		// read_pdf: 'http://jootu.asuscomm.com:1234/emergency_drill/public/',						    //nas
		// download_path:'/emergency_drill/public/api/download',                                        //nas
		// maxPlay_path:'/emergency_drill/public/#/maxplay',		                                    //nas
		// maxGroup_path:'/emergency_drill/public/#/MaxGroup',		                                    //nas

	    file_path: 'http://www.jiaxingyanlian.com/storage/',			        //素材文件查看路径       联通
	    // read_pdf: 'http://www.jiaxingyanlian.com/',						    //pdf查看路径           联通


		// file_path: 'http://jootu.asuscomm.com:1234/emergency_drill/public/storage/',     //本地/nas         素材文件查看路径
		// file_path: 'http://emergency_drill.test/public/storage/',     //本地/nas         素材文件查看路径
		material_path: '/api/attachment',                                                   //本地/联通         设计环节素材上传路径
		pdf_path: '/api/database',			                                                //本地/联通         pdf上传路径
		read_pdf: '/pdfApi/storage/',		                                                //本地
		download_path:'/api/download',                                                      //本地/联通         下载文档路径
		maxPlay_path:'/#/maxplay',                                                          //本地/联通         导演开始页面的打开大屏
		maxGroup_path:'/#/MaxGroup',		                                                //本地/联通         导演签到页面的打开大屏
    },
    mutations: {
        increment (state,n) {
            state.user = n;
        }
    }
});

export default store;




