import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import axios from 'axios';
import Vuex from 'vuex';
import store from './vuex/store';
import 'babel-polyfill';

router.beforeEach((to,from,next) => {
  //这里判断用户是否登录
  if( to.path == '/login'){//是从登陆页来的，登陆成功
	  store.state.host = window.location.host;
	  store.state.protocol = window.location.protocol;
      next();
  }else{//不是从登陆页来的，
	  store.state.host = window.location.host;
	  store.state.protocol = window.location.protocol+'//';
    if (!localStorage.getItem("user")) {//用户没登陆过
      next({
        path: '/login',
      })
    } else {//用户登陆过了，直接进入首页
        next();
    }
  }
});

axios.defaults.baseURL = 'http://localhost/emergency_drill/public';	
// axios.defaults.baseURL = 'http://www.jiaxingyanlian.com';	//正式

// http request 拦截器
axios.interceptors.request.use(
  config => {
    if (localStorage.getItem("user")) {  // 判断是否存在token，如果存在的话，则每个http header都加上token
      config.headers.Authorization = `Bearer ${JSON.parse(localStorage.getItem("user")).token}`;
    }
    return config;
  },
  err => {
    return Promise.reject(err);
  });
// http response 拦截器
axios.interceptors.response.use(
  response => {
    if(response.data.status == 401){
      localStorage.removeItem('user');
      store.state.user = '';
      router.replace({
        path: '/login',
        query: {redirect: router.currentRoute.fullPath}
      })
    }
    return response;
  },
  error => {
    if (error.response) {
      switch (error.response.status) {
        case 401:
          localStorage.removeItem('user');
          store.state.user = '';
          router.replace({
            path: '/login',
            query: {redirect: router.currentRoute.fullPath}
          })
      }
    }
    return Promise.reject(error.response.data)   // 返回接口返回的错误信息
  });


Vue.use(ElementUI);
Vue.use(Vuex);
Vue.prototype.$axios = axios;
Vue.config.productionTip = false

import './assets/icon/iconfont.css';

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



